import {
    Box, ListItemButton, ListItemText, Typography, styled
} from "@mui/material";
import { format } from "date-fns-jalali";
import flatten from "lodash/flatten";
import sortBy from "lodash/sortBy";
import { useState } from "react";
import { useSearchParams } from "react-router-dom";
import { useAuth } from "../../../../auth";
import ResponsiveTable from "../../../../components/ResponsiveTable";
import Text from "../../../../components/Text";
import useAfterFirstRender from "../../../../utils/useAfterFirstRender";
import useGetRowNumber from "../../../../utils/useGetRowNumber";


const ClamppedText = styled(Typography)(({ theme }) => ({
    overflow: 'hidden',
    display: '-webkit-box',
    '-webkit-box-orient': 'vertical',
    '-webkit-line-clamp': '2',
}));

const DataTable = ({
    loading,
    data,
    meta,
    infiniteScrollProps,
}) => {

    const { inject } = useAuth();

    const [searchParams, setSearchParams] = useSearchParams();

    const [sort, setSort] = useState(
        searchParams.has('sort') ? searchParams.get('sort') : '-createdAt'
    );

    useAfterFirstRender(() => {
        setSearchParams({
            ...Object.fromEntries(searchParams),
            sort
        })
    }, [sort]);

    const getRowNumber = useGetRowNumber();


    return (
        <>
            <ResponsiveTable
                data={data}
                loading={loading}
                infiniteScrollProps={infiniteScrollProps}
                definitions={sortBy([
                    {
                        title: 'ردیف',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center",
                            width: 100
                        },
                        renderCell: (row, index) => <Text>{getRowNumber(index)}</Text>,
                        priority: 100
                    }, {
                        title: 'گیرنده',
                        rowCellProps: {
                            width: 100
                        },
                        renderCell: row => (
                            <Box sx={{ display: 'flex', alignItems: 'center' }}>
                                {/* <Avatar
                                    sx={{ width: 30, height: 30, fontSize: '1.15em', mr: 1 }}
                                    src={row.avatar}
                                /> */}
                                <strong><Text>{row.receiver}</Text></strong>
                            </Box>
                        ),
                        priority: 200
                    }, {
                        field: 'content',
                        title: 'متن پیامک',
                        // headerCellProps: {
                        //     align: "center"
                        // },
                        rowCellProps: {
                            // align: "center",
                            width: 300
                        },
                        render: row => <ClamppedText whiteSpace="pre"><Text>{row.content}</Text></ClamppedText> || '-',
                        priority: 300
                    }, {
                        field: 'count',
                        title: 'تعداد پیامک',
                        // headerCellProps: {
                        //     align: "center"
                        // },
                        rowCellProps: {
                            // align: "center",
                            width: 100
                        },
                        // render: row => <Typography><Text>{row.content}</Text></Typography> || '-',
                        priority: 400
                    }, {
                        field: 'sentAt',
                        title: 'تاریخ ارسال',
                        // headerCellProps: {
                        //     align: "center"
                        // },
                        rowCellProps: {
                            // align: "center",
                            width: 100
                        },
                        render: row => <Text>{format(new Date(row.sentAt), 'yyyy/MM/dd HH:mm')}</Text> || '-',
                        priority: 500
                    }, {
                        field: 'status',
                        title: 'وضعیت',
                        // headerCellProps: {
                        //     align: "center"
                        // },
                        rowCellProps: {
                            // align: "center",
                            width: 100
                        },
                        // render: row => <Typography><Text>{row.content}</Text></Typography> || '-',
                        priority: 600
                    },
                    ...flatten((inject('Sms', 'History.definitions') || []))
                ], o => o.priority)}
                renderSheetTitle={row => <ClamppedText whiteSpace="pre"><Text>{row.content}</Text></ClamppedText>}
                renderListItemChildren={(row) => (
                    <ListItemButton 
                        // onClick={() => navigateTask(taskDetails(row))}
                    >
                        {/* <ListItemAvatar>
                            <Avatar src={row.avatar} />
                        </ListItemAvatar> */}
                        <ListItemText
                            primary={<ClamppedText whiteSpace="pre"><Text>{row.content}</Text></ClamppedText>}
                        // secondary={row.roles && row.roles.length > 0 ? <Text>{row.roles.map(r => r.name).join(', ')}</Text> : '-'}
                        />
                    </ListItemButton>
                )}

                tableRowProps={row => ({
                    // hover: true,
                    striped: true,
                    // onClick: () => navigateTask(taskDetails(row))
                })}

                paginationProps={{
                    count: meta.total,
                    rowsPerPage: searchParams.get('perPage') || 25,
                    page: searchParams.get('page') || 0,
                    onPageChange: (e, page) => setSearchParams({ ...Object.fromEntries([...searchParams]), page }),
                    onRowsPerPageChange: e => setSearchParams({ ...Object.fromEntries([...searchParams]), perPage: e.target.value })
                }}

            // actions={[
            //     {
            //         title: 'ویرایش',
            //         icon: <Edit />,
            //         onClick: onClickEdit,
            //         authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('person:edit')
            //     },
            //     {
            //         title: 'حذف',
            //         icon: <DeleteOutline />,
            //         onClick: person => onClickDelete(person, false),
            //         authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('person:delete')
            //     }
            // ]}
            />
        </>
    )
}

export default DataTable