import AddIcon from "@mui/icons-material/Add";
import MenuOutlined from "@mui/icons-material/MenuOutlined";
import {
    Box, Card, Divider, Stack
} from "@mui/material";
import flatten from "lodash/flatten";
import sortBy from "lodash/sortBy";
import { useSnackbar } from "notistack";
import { useSearchParams } from "react-router-dom";
import { useMiddleware, useTask } from "../../../../Application";
import { useAuth } from "../../../../auth";
import ResponsiveActionBar from "../../../../components/ResponsiveActionBar";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";
import useDialogProps from "../../../../utils/useDialogProps";
import useNavigateTask from "../../../../utils/useNavigateTask";
import useResponsiveTableFetch from "../../../../utils/useResponsiveTableFetch";
// import PersonDeleteDialog from "../../components/PersonDeleteDialog";
// import PersonFormDialog from "../../components/PersonFormDialog";
import DataTable from "./DataTable";
// import FilterForm from "./FilterForm";


const History = () => {

    const { inject } = useAuth();

    const middleware = useMiddleware('Sms.main');

    const [searchParams] = useSearchParams();
    const navigateTask = useNavigateTask();

    const { data, total, loading, loadMore, reload } = useResponsiveTableFetch({
        handleFetch: params => middleware.list({
            ...params,
            ...Object.fromEntries([...searchParams])
        }),
        loadDeps: [searchParams],
        infiniteScrollDefaultParams: {
            page: searchParams.has('page') ? searchParams.get('page') : 0,
            perPage: searchParams.has('perPage') ? searchParams.get('perPage') : 25,
        }
    })

    const { enqueueSnackbar } = useSnackbar();

    const [formDialogProps, openFormDialog, closeFormDialog] = useDialogProps("form");
    const handleCreateOrUpdate = (message) => {
        enqueueSnackbar(message);
        reload();
        closeFormDialog();
    }

    const [deleteDialogProps, openDeleteDialog, closeDeleteDialog] = useDialogProps("delete", {
        hashDeps: ["person"]
    });
    const handleDelete = () => {
        enqueueSnackbar('شخص حذف گردید');
        closeDeleteDialog();
        reload();
    }

    const injectedHooks = Object.assign({}, ...(inject('Sms', 'History.Hooks') || []));

    const task = useTask('Sms.history');


    return (
        <TabView {...task}>
            <ResponsiveContainer>
                <Box
                    sx={{
                        display: "flex",
                        mb: { xs: 0, sm: 1 }
                    }}
                >
                    <Box sx={{ flexGrow: 1 }} />
                    <Stack direction="row" spacing={1}>
                        <ResponsiveActionBar
                            actions={sortBy([
                                // {
                                //     title: 'شخص جدید',
                                //     icon: <AddIcon />,
                                //     onClick: () => {
                                //         openFormDialog({
                                //             onCreateOrUpdate: () => handleCreateOrUpdate(`مشخصات شخص ثبت گردید.`)
                                //         })
                                //     },
                                //     authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('person:edit'),
                                //     priority: 100
                                // },
                                ...flatten((inject('Sms', 'History.ActionsList', { data, loading, reload, ...injectedHooks }) || []))
                            ], o => o.priority)}
                            ResponsiveActionIcon={<MenuOutlined />}
                            responsiveActionProps={{ color: 'primary' }}
                        />
                    </Stack>
                </Box>
                <Card variant="outlined" sx={{ flexGrow: 1 }}>
                    {/* <FilterForm /> */}
                    {/* <Divider /> */}
                    <DataTable
                        data={data}
                        loading={loading}
                        meta={{ total }}
                        infiniteScrollProps={{
                            loading: false,
                            hasNextPage: data.length < total,
                            onLoadMore: loadMore
                        }}
                        onClickEdit={item => {
                            openFormDialog({
                                defaultValues: item,
                                onCreateOrUpdate: () => handleCreateOrUpdate(`مشخصات شخص بروزرسانی گردید.`)
                            })
                        }}
                        onClickDelete={(person, permanent) => {
                            openDeleteDialog({ person, permanent });
                        }}
                    />
                </Card>

                {/* <PersonFormDialog
                    {...formDialogProps}
                /> */}

                {/* <PersonDeleteDialog
                    {...deleteDialogProps}
                    onDelete={handleDelete}
                /> */}

                {inject('Sms', 'History.Footer', { data, loading, reload, ...injectedHooks })}
            </ResponsiveContainer>
        </TabView>
    )
}

export default History